@import '../../styles/variables.scss';

.header {
    padding: 16px 0;
    z-index: 5;
    background-color: $color-green;
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__cart {
        padding: 10px 15px;
        display: flex;
        align-items: center;
        background-color: $color-white;
        border-radius: 6px;
        transition: transform 0.2s ease-in-out;
        cursor: pointer;
        &:hover {
            transform: translateX(-10px);
        }
    }
    &__cart-img {
        width: 30px;
        height: 30px;
        margin-right: 10px;
    }
    &__cart-text {
        color: $color-green;
        font-weight: 600;
    }
    &__mobile-menu {
        background-color: $color-green;
        display: none;
        left: 0;
        padding-top: 80px;
        position: fixed;
        top: -100%;
        transition: top .3s ease-in-out;
        width: 100%;
        z-index: 46;
        &--active {
            top: 0;
        }
        @media screen and (max-width: 900px) {
            display: flex;
            justify-content: center;
        }
    }
}

.overlay {
    position: fixed;
    background-color: transparent;
    &--active {
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba($color-black, 0.6);
        z-index: 45;
    }
}